import { createSlice } from "@reduxjs/toolkit";


const toolkitSlice = createSlice({
    name: 'toolkit',
    initialState: {
        phone: '79991112233',
        email: 'dima18127@yandex.ru',
        nick: 'dima181271',
        name: 'Дмитрий',
        sername:'Макаров',
        sex: 'man',
        advantages: [{ value: "1", focus: "true" }],
        checkbox: ['2'],
        radiobox: '3',
        aboutInp: '',
    },
    reducers: {
        addEmail(state, action){
            state.email = action.payload
        },
        addPhone(state,action){
            state.phone = action.payload
        },
        setNick(state,action){
            state.nick = action.payload
        },
        setName(state,action){
            state.name = action.payload
        },
        setSerName(state,action){
            state.sername = action.payload
        },
        setMail(state,action){
            state.sex = action.payload
        },
        setADV(state, action){
            state.advantages = action.payload
        },
        setCheck(state, action){
            state.checkbox = action.payload
        },
        setRadio(state, action){
            state.radiobox = action.payload
        },
        setAbout(state, action){
            state.aboutInp = action.payload
        },

    }
    
})
export default toolkitSlice.reducer;
export const  {addEmail,addPhone,setNick,setName,setSerName,setMail,setADV,setCheck,setRadio,setAbout} = toolkitSlice.actions;
