import React, { useEffect } from 'react'
import s from './Modal.module.css'
import { useNavigate } from 'react-router';

const Modal = ({formSubmitted,formStatus,setform}) => {
    // formStatus=true;
const navigate = useNavigate();
let styleBtn = `btn`;
let btntext ='На главную';
let imageSrc = 'icons/success.svg';
let textStatus = "Форма успешно отправлена";
let onclickHandler = ()=> {
    setform(false);
    navigate("/");
}

if(formStatus) {
    styleBtn = `${s.buttonClose} btn`;
    btntext = 'Закрыть'
    imageSrc = 'icons/error.svg'
    textStatus="Ошибка"
    onclickHandler = ()=> {
        setform(!formSubmitted)
    }
}
  return (
    <div className={s.wrap}>
        <div className={s.modal}>
        <p className={`${s.formtext} ${s.left}`}>{textStatus}</p>
        <img src={imageSrc} alt="success" />
        <button 
        className={s.closeButton}
        onClick={onclickHandler}
        ></button>
        <button 
        id="button-close"
        className={styleBtn}
        onClick={onclickHandler}
        >{btntext}</button>
        {/* <button id="button-close" className={s.buttonClose}>на главную</button> */}
        </div>
    </div>
  )
}

export default Modal

